<template>
	<div id="finance-account">
		<public-header title="提现账户"></public-header>
		<div class="content">
			<div class="account-title" v-if="status.bank == 1 && status.alipay == 1">
				<div class="account-group flex">
					<div class="bank" :class="{active: filter.items.type == 'bank'}" @click="onToggleStatus('bank')">提现到银行账户</div>
					<div class="alipay" :class="{active: filter.items.type == 'alipay'}" @click="onToggleStatus('alipay')">提现到支付宝账户</div>
				</div>
			</div>
			<div class="padding-15 font-14 c-gray" v-if="status.bank == 1 && status.alipay != 1">提现到银行账户</div>
			<div class="padding-15 font-14 c-gray" v-if="status.bank != 1 && status.alipay == 1">提现到支付宝账户</div>
			<template v-if="filter.items.type == 'bank'">
				<van-cell-group>
					<van-cell title="银行">
						<div slot="right-icon" class="flex" @click="onBankPicker()">
							<span>{{bank.title? bank.title:'请选择开户银行'}}</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-field
						v-model="bank.account"
						label="银行卡号"
						placeholder="请输入银行卡号"
						input-align="right"
					/>
					<van-field
						v-model="bank.realname"
						label="开户人姓名"
						placeholder="请输入开户人姓名"
						input-align="right"
					/>
				</van-cell-group>
			</template>
			<template v-else>
				<van-cell-group>
					<van-field
						v-model="alipay.account"
						label="支付宝账号"
						placeholder="请输入支付宝账号"
						input-align="right"
					/>
					<van-field
						v-model="alipay.realname"
						label="支付宝姓名"
						placeholder="请输入支付宝姓名"
						input-align="right"
					/>
				</van-cell-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block :disabled="!islegal" class="bg-danger font-16" @click="onSubmit">提交</van-button>
			</div>
		</div>
		<!--银行选择器-->
		<van-popup v-model="bankPicker" position="bottom" class="popup-bank">
			<div class="popup-content">
				<van-picker
					show-toolbar
					value-key="title"
					:columns="bank_list"
					@cancel="onBankPicker"
					@confirm="onConfirmBank"
				/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				alipay: {
					realname: '',
					account: ''
				},
				bank: {
					id: '',
					title: '',
					realname: '',
					account: ''
				},
				bank_list: [],
				bankPicker: false,
				filter: {
					items: {
						type: 'bank'
					}
				},
				status: {
					bank: 1,
					alipay: 1,
				},
				showPreLoading:true,
				islegal: false
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/member/account',
					data: {},
					success: (res) => {
						that.bank_list = [...that.bank_list,...res.bank_list];
						that.bank = that.util.extend(that.bank, res.bank);
						that.alipay = that.util.extend(that.alipay, res.alipay);
						that.status = that.util.extend(that.status, res.status);
						if(that.status.bank != 1) {
							this.filter.items.type = 'alipay';
						}
						that.islegal = true;
					}
				});
			},
			onBankPicker() {
				this.bankPicker = !this.bankPicker;
			},
			onConfirmBank(picker) {
				var that = this;
				that.bank.id = picker.id;
				that.bank.title = picker.title;
				that.onBankPicker();
			},
			onToggleStatus(type) {
				if(this.filter.items.type == type) {
					return;
				}
				this.filter.items.type = type;
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return;
				}
				if(that.filter.items.type == 'bank') {
					if(!that.bank.title) {
						that.util.$toast('请选择开户银行', '', 1000);
						return;
					}
					if(!that.bank.account) {
						that.util.$toast('银行卡号不能为空', '', 1000);
						return;
					}
					if(!that.bank.realname) {
						that.util.$toast('开户人姓名不能为空', '', 1000);
						return;
					}
				} else {
					if(!that.alipay.account) {
						that.util.$toast('支付宝账号不能为空', '', 1000);
						return;
					}
					if(!that.alipay.realname) {
						that.util.$toast('支付宝姓名不能为空', '', 1000);
						return;
					}
				}
				that.islegal = false;
				var params = {
					bank: that.bank,
					alipay: that.alipay
				};
				setData({
					vue: that,
					url: 'wmall/member/account',
					data: {
						params: JSON.stringify(params)
					},
					success: () => {
						that.util.$toast('提现账户设置成功', that.util.getUrl({path: '/plugin/pages/spread/commission'}), 1500);
						return;
					},
					fail: () => {
						that.util.$toast('提现账户设置失败','',1500);
						that.islegal = true;
						return;
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#finance-account .account-title{
		padding: 15px 30px;
	}
	#finance-account .account-title .account-group{
		width: 100%;
		height: 30px;
		border: 1px solid #FF2D4B;
		border-radius: 30px;
		background-color: #fff;
	}
	#finance-account .account-title .account-group .bank{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50%;
		height: 100%;
		color: #FF2D4B;
		font-size: 14px;
		border-radius: 30px 0px 0px 30px;
	}
	#finance-account .account-title .account-group .alipay{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50%;
		height: 100%;
		color: #FF2D4B;
		font-size: 14px;
		border-radius: 0px 30px 30px 0px;
		border-left: 1px solid #FF2D4B;
	}
	#finance-account .account-title .account-group .bank.active,#finance-account .account-title .account-group .alipay.active{
		color: #fff;
		background-color: #FF2D4B;
	}
	#finance-account .popup-bank .van-picker__cancel{
		color: #999!important;
	}
	#finance-account .popup-bank .van-picker__confirm{
		color: #ff2d4b!important;
	}
</style>


